import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import PlusBtn from '../../assets/plus_btn.png';
import { SERVER_URL } from '../../constants';
import ProductBtn from '../common/product/ProductBtn';

const BookContainer = styled.div`
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	gap: 2rem;
	margin-top: 2rem;
	@media ${({ theme }) => theme.size.mobile} {
		flex-wrap: nowrap;
		max-width: 34rem;
		overflow: auto hidden;
		${({ theme }) => theme.ScrollbarStyle()}
	}
`;

const BookRegister = styled.div`
	border: 1px dashed ${({ theme }) => theme.grayColor2};
	border-radius: 1rem;
	width: 31.5rem;
	height: 25.2rem;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	@media ${({ theme }) => theme.size.mobile} {
		display: none;
	}
`;

const RegisterBtn = styled.button`
	border: none;
	background-color: transparent;
	margin-bottom: 1rem;
	text-align: center;
`;

const PlusBtnImg = styled.img`
	width: 7rem;
	margin-bottom: 1rem;
`;

const RegisterTitle = styled.p`
	font-weight: 700;
	font-size: 2.4rem;
	color: ${({ theme }) => theme.grayColor2};
`;

function ProductRegister() {
	const token = JSON.parse(localStorage.getItem('user')).token;
	const MyAccountName = JSON.parse(localStorage.getItem('user')).accountname;
	const [books, setBooks] = useState(null);
	async function MyBookList() {
		try {
			const res = await fetch(SERVER_URL + `/product/${MyAccountName}`, {
				method: 'GET',
				headers: {
					Authorization: `Bearer ${token}`,
					'Content-type': 'application/json',
				},
			});
			const result = await res.json();
			setBooks(result.product);
		} catch (error) {
			console.error(error);
		}
	}

	useEffect(() => {
		MyBookList();
	}, []);

	return (
		<>
			<BookContainer>
				{books?.map((item) => (
					<ProductBtn key={item.id} item={item} />
				))}
				<BookRegister>
					<RegisterBtn as={Link} to="/product">
						<PlusBtnImg src={PlusBtn} alt="" />
						<RegisterTitle>상품등록</RegisterTitle>
					</RegisterBtn>
				</BookRegister>
			</BookContainer>
		</>
	);
}

export default ProductRegister;
